import React from 'react';
import './Item.css';
import ContentItem from '../../model/ContentItem';
import ItemContent from '../content/ItemContent';

class Item extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            content_index: -1
        }
        this.open_reminder = this.open_reminder.bind(this);
        this.close_reminder = this.close_reminder.bind(this);
        this.scroll_container = this.scroll_container.bind(this);
    }

    open_reminder(){
        this.setState({content_index: 1});
    }

    close_reminder(date, text){
        if(text){
            let content = new ContentItem(date, text);
            this.props.item.content.push(content);
            this.props.contacts.saveToLocalStorage();  
        }
        this.setState({content_index: -1});
    }
    
    scroll_container(){
        let elements_container=document.getElementsByClassName("elements-container");
        console.log(elements_container);
        for (let i = 0; i < elements_container.length; i++) {
            elements_container[i].scrollTop = elements_container[i].scrollHeight - elements_container[i].clientHeight;   
        }
    }
    
    render(){
        return (
            <div className='contact-element'>
                <div className="contacts-item d-flex align-items-center" >
                    <div className="contacts-initials d-flex" style={{'backgroundColor':this.props.item.color}} onClick={this.open_reminder}>
                        <span>{this.props.item.name.split(' ').map(word =>
                                word.charAt(0).toUpperCase()).join('')}</span>
                    </div>
                    <div className="contacts-item-content d-flex flex-column flex-grow-1">
                        <span id="name" className="data">{this.props.item.name.toLowerCase().split(' ').map(word =>
                                word.charAt(0).toUpperCase() + word.slice(1)).join(' ')}</span>
                        <span id="address" className="data">{this.props.item.address.split(' ').map(word =>
                                word.charAt(0).toUpperCase() + word.slice(1)).join(' ')}</span>

                        <span id="number" className="data">{this.props.item.number}</span>
                    </div>

                    <i className={`fa fa-trash ${this.props.search_index === 1 ? 'hide':''}`} title="delete" onClick={(e) => this.props.onDeleteItem(this.props.item)}></i>
                    <i className={`fa fa-trash ${this.props.search_index === -1 ? 'hide':'show'}`}></i>

                </div>

                <div className={`hide ${this.state.content_index === 1 ? 'reminder' : ''}`} > 
                    <div className='d-flex justify-content-between'>
                        <span>REMINDER</span>
                        <i className='fa fa-times' onClick={(event)=>{this.close_reminder(event.target.parentElement.nextSibling.nextSibling.children[0].innerHTML,event.target.parentElement.nextSibling.nextSibling.children[1].children[0].value); 
                        event.target.parentElement.nextSibling.nextSibling.children[1].children[0].value=''}}></i>
                    </div>

                    <div className='elements-container' onMouseEnter={this.scroll_container}>
                        {this.props.item.content.map((value,index) => <ItemContent key={index} index={index} content={value} item={this.props.item} delete_content={this.props.delete_content}/>)}
                    </div>

                    <div className='d-flex'>
                        <div id='date' className='col-4 col-sm-3'>{(new Date().toLocaleString("ro-RO", {day:'numeric',month:'numeric',year:'numeric' }))}</div>
                        <div className='col-8 col-sm-9'>
                            <textarea id='textarea' className='form-control' onMouseEnter={(event)=> event.target.focus()}></textarea>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default Item;