import React from 'react';
import './ItemContent.css';

function ContentItem(props){
    return (
        <div className="d-flex">
            <div className="col-4 col-sm-3">{props.content.date}</div>
            <div className="col-8 col-sm-9 d-flex justify-content-between">
                <span>{props.content.text}</span>
                <i className="fa fa-minus" onClick={()=>props.delete_content(props.item, props.index)}></i>
            </div>
        </div>
    )
}

export default ContentItem;