import ContactItem from "./ContactItem";

class Contacts{

    
    constructor(){
        this.items = this.loadFromLocalStorage();
    }

    add(item){
        this.items.push(item);        
        this.items.sort((a, b) => (a.name.toLowerCase() > b.name.toLowerCase()) ? 1 : -1);
        this.saveToLocalStorage();

        return this.items;
    }

    delete(item){
        this.items.splice(this.items.indexOf(item), 1);
        this.saveToLocalStorage();

        // return this.items;
    }

    saveToLocalStorage(){
        localStorage.setItem('contacts-react', JSON.stringify(this.items));
    }

    loadFromLocalStorage(){
        var json = localStorage.getItem('contacts-react');

        if(json === null)
            return [];


        let jsonParsed = JSON.parse(json);

        if(jsonParsed.length === 0)
            return [];

        let contactItems = [];

        for (let i = 0; i < jsonParsed.length; i++) {
            contactItems.push(ContactItem.fromJSON(jsonParsed[i]));                
        }

        contactItems = contactItems.sort((a,b) => (a.name.toLowerCase() > b.name.toLowerCase()) ? 1 : -1);

        return contactItems;
        
    }
}

export default Contacts;