import React from 'react';
import './Footer.css';

class Footer extends React.Component {

  constructor(props){
    super(props);

    this.state = {
      name: '',
      number: '',
      address: ''
    }

    this.handleChangeName = this.handleChangeName.bind(this);
    this.handleChangeNumber = this.handleChangeNumber.bind(this);
    this.handleChangeAddress = this.handleChangeAddress.bind(this);
  }

  handleChangeName(event){
    this.setState({ name: event.target.value });
  }

  handleChangeNumber(event){
    this.setState({ number: event.target.value });
  }

  handleChangeAddress(event){
    this.setState({ address: event.target.value });
  }

  enterData(event){
    let myAddButton = document.getElementById("add-btn");
    let nameField = document.getElementById("name-field");
    if(event.keyCode === 13){      
      myAddButton.click();
      nameField.focus();
    }
  }

  setFocusNumber(event){
    let numberField = document.getElementById("number-field");
    if(event.key === "Enter"){      
      numberField.focus();
    }
  }

  setFocusAddress(event){
    let addressField = document.getElementById("address-field");
    if(event.key === "Enter"){      
      addressField.focus();
    }
  }

  render(){
    return (
      <footer>
        <div className='d-flex align-items-center'>
          <div className='d-flex flex-grow-1 flex-column main-container'>
            <div className="d-flex flex-grow-1 content">
              <div className ={`input-icons d-flex flex-grow-1 ${this.props.warning_name === 1 ? 'warning' : ''}`}> 
                <i className="fa fa-user fa-fw icon"></i> 
                <input id="name-field" onKeyUp={this.setFocusNumber} className="input-field flex-grow-1" value={this.state.name} onChange={this.handleChangeName} placeholder="contact's name..." /> 
              </div> 
              <div className={`input-icons d-flex flex-grow-1 ${this.props.warning_phone === 1 ? 'warning' : ''}`}> 
                <i className="fas fa-phone-alt fa-fw icon"></i>
                <input id="number-field" onKeyUp={this.setFocusAddress} className="input-field flex-grow-1" value={this.state.number} onChange={this.handleChangeNumber} placeholder="contact's number..." /> 
              </div>
            </div>
            <div className={`input-icons ${this.props.warning_address === 1 ? 'warning' : ''}`}> 
              <i className="fas fa-home fa-fw icon"></i>
              <input id="address-field" onKeyUp={this.enterData} className="input-field flex-grow-1" value={this.state.address} onChange={this.handleChangeAddress} placeholder="contact's address..." /> 
            </div>
          </div>
          <span id="add-btn" onClick={() => {
            this.props.createItem(this.state.name, this.state.number, this.state.address); 
            if(this.state.name && this.state.number && this.state.address){
              this.setState({name: ''});
              this.setState({number: ''});
              this.setState({address: ''});
            } 
            }}>Add</span>
          </div>
      </footer>
    )
  }
}

export default Footer;
