import React from 'react';
import './Header.css';

function Header(props){
    return (
        <header className='d-flex flex-column'>
            <div id="header" className='d-flex justify-content-center align-items-center'>
                <span>MY CONTACTS</span>
                <i className="fas fa-search" onClick={props.open_search_field} id="search-btn"></i>
            </div>
            <div className={`search ${props.search_index === -1 ? 'hide' : ''}`}>
                <input type="search" id="search-field" onInput={(event) => props.searchWithField(event.target.value)} placeholder="search by name" autoFocus />
                <button onClick={(event)=>{props.close_search_field(); event.target.previousElementSibling.value=''}}>Close</button>
            </div>
        </header>
    )
}

export default Header;










