import React from 'react';
import './Body.css';
import Item from '../item/Item';


function Body(props){
    return (
        <div className="items-container">
            <span className="no-items-message">No Contacts</span>
            {props.items.map(function(value, index){
                return <Item key={index} item={value} onDeleteItem={props.onDeleteItem} contacts={props.contacts} delete_content={props.delete_content} search_index={props.search_index}></Item>
            })}
        </div>
    )
  }
export default Body;
