import React from 'react';
import './App.css';
import Header from './components/header/Header';
import Body from './components/body/Body';
import Footer from './components/footer/Footer';
import ContactItem from './model/ContactItem';
import Contacts from './model/Contacts';

class App extends React.Component{

  constructor(props){
    super(props);

    this.contacts = new Contacts();
    console.log(this.contacts);
    this.state = {
      items: this.contacts.items,
      warning_user: -1,
      warning_phone: -1,
      warning_address: -1,
      search_index : -1
    }

    this.open_search_field = ()=>{
      this.setState({search_index: 1});
    }
  

    this.createItem = this.createItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.searchWithField = this.searchWithField.bind(this);
    this.close_search_field = this.close_search_field.bind(this);
    this.delete_content = this.delete_content.bind(this);

  }

  createItem(name, number, address){

    if(name && number && address){
      let item = new ContactItem(name, number, address);
      let items = this.contacts.add(item);
      this.setState({items: items});
      this.setState({warning_user: -1});
      this.setState({warning_phone: -1});
      this.setState({warning_address: -1});
    } else {
      if(name === '') this.setState({warning_user: 1});
      if(number === '') this.setState({warning_phone: 1});
      if(address === '') this.setState({warning_address: 1});
    }
  }

  deleteItem(item){
    this.contacts.delete(item);
    this.setState({items: this.contacts.items});
  }


  searchWithField(search_query){      

    let items = this.contacts.loadFromLocalStorage();
    let result = [];
    for (let i = 0; i < items.length; i++) {
      if(items[i].name.toLowerCase().includes(search_query.toLowerCase())){
        result.push(items[i]);
      }      
    }
    this.setState({items: result});
  }

  close_search_field(){
    this.setState({search_index: -1, items: this.contacts.loadFromLocalStorage()});
  }

  delete_content(item, index){
    item.content.splice(index, 1);
    this.contacts.saveToLocalStorage();
    this.setState({items: this.contacts.items});
}


  render (){
    return (
      <div id="app-container" className='d-flex flex-column'>
        <Header open_search_field={this.open_search_field} close_search_field={this.close_search_field} searchWithField={this.searchWithField} search_index={this.state.search_index} ></Header>
        <Body items={this.state.items} onDeleteItem={this.deleteItem} close_reminder={this.close_reminder} contacts={this.contacts} delete_content={this.delete_content} search_index={this.state.search_index}/>
        <Footer createItem = {this.createItem} warning_name={this.state.warning_user} warning_phone={this.state.warning_phone} warning_address={this.state.warning_address} />
      </div>
    )
  }
}

export default App;
